.navbar {
  height: 60px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 90px;
  position: fixed;
  z-index: 1;
  box-sizing: border-box;
  color: white;
  background-image: linear-gradient(45deg, #0784a7, #000000);
  background-size: 200% 200%;
 
}
.navbar ul {
  display: flex;
  list-style-type: none;
  text-decoration: none;
}
.navbar ul li {
  margin: 20px;
  color: white;
  font-size: 18px;
  cursor: pointer;
  font-weight: bold;
 
}

#checkbox {
  display: none;
}
.fa-bars {
  display: none;
}

@media only screen and (max-width: 900px) {
  .navbar {
    padding: 0 20px;
  }
  .navbar ul {
    background-color: rgb(4 83 106);
    position: fixed;
    top: 60px;
    left: -50%;
    height: 100vh;
    flex-direction: column;
    width: 50%;
    transition: all 0.5s linear;
  }

  #checkbox:checked ~ .nav-ul {
    left: 0;
  }
  .fa-bars {
    font-size: 25px;
    display: block;
  }
  .h12 {
    padding-top: 11%;
    margin-top: 4%;
    border: 3px solid black;
  }
}